import Button from '@/components/button'
import React, { useEffect, useState } from 'react'

const colorVars = [
  { dark: '#323233', light: '#E5E5E5', name: 'header', desc: '顶部' },
  { dark: '#2D2D2D', light: '#ECECEC', name: 'tabBg', desc: '标签栏' },
  { dark: '#252526', light: '#F3F3F3', name: 'treeBg', desc: '树' },
  { dark: '#333333', light: '#2C2C2C', name: 'nav', desc: '导航栏' },
  { dark: '#37373D', light: '#E4E6F2', name: 'treeActive', desc: '树项目选中' },
  { dark: '#292D2E', light: '#E8E8E8', name: 'treeHover', desc: '树项目悬停' },
  { dark: '#858585', light: '#808080', name: 'iconBg', desc: '图表' },
  { dark: '#4D4D4E', light: '#C4C4C4', name: 'tagBg', desc: '标签' },
  { dark: '#969696', light: '#6A6A6A', name: 'tabText', desc: '标签文本' },
  { dark: '#1E1E1E', light: '#FFFFFF', name: 'mainBg', desc: '编辑器背景色' },
  { dark: '#474747', light: '#D7D7D7', name: 'border', desc: '边框' },
  { dark: '#CCCCCC', light: '#333333', name: 'text', desc: '文字' }
]

export default React.memo((props: any) => {
  const [theme, setTheme] = useState('light')

  useEffect(() => {
    console.log('old theme', theme)
    if (
      theme === 'dark' ||
      (!('theme' in localStorage) && window.matchMedia('(prefers-color -scheme: dark)').matches)
    ) {
      setTheme('dark')
      document.querySelector('html')?.classList.add('dark')
    } else {
      document.querySelector('html')?.classList.remove('dark')
    }
    localStorage.theme = theme
    console.log(theme)
  }, [theme])

  const toggleTheme = () => setTheme(theme === 'dark' ? 'light' : 'dark')

  const colorVar = (name: string) => {
    return (colorVars.find((i) => i.name === name) as any)[theme]
  }

  return (
    <>
      <div style={{ display: 'flex', height: '100vh' }}>
        <div style={{ background: '#fff', width: 'calc(100% - 220px)' }}>
          <div
            style={{
              height: '30px',
              background: colorVar('header'),
              textAlign: 'center',
              fontSize: '11px',
              lineHeight: '30px',
              color: colorVar('text')
            }}
          >
            header / project
          </div>
          <div style={{ height: 'calc(100% - 30px)', display: 'flex' }}>
            <div
              style={{
                width: '30px',
                height: '100%',
                padding: '10px 8px',
                background: colorVar('nav')
              }}
            >
              <div
                style={{
                  width: '15px',
                  height: '15px',
                  textAlign: 'center',
                  background: colorVar('iconBg')
                }}
              ></div>
            </div>
            <div style={{ width: 'calc(100% - 30px)', display: 'flex' }}>
              <div
                style={{
                  width: '240px',
                  height: '100%',
                  fontSize: '12px',
                  background: colorVar('treeBg'),
                  color: colorVar('text')
                }}
              >
                <div style={{ display: 'flex', padding: '10px 4px', color: colorVar('text') }}>
                  打开编辑器
                  <div
                    style={{
                      marginLeft: '5px',
                      width: '50px',
                      color: '#333',
                      background: colorVar('tagBg'),
                      fontSize: '11px',
                      height: '18px',
                      borderRadius: '2px',
                      padding: '0px 4px'
                    }}
                  >
                    未保存
                  </div>
                </div>
                <div
                  style={{
                    background: colorVar('treeHover'),
                    borderBottom: `1px solid ${colorVar('border')}`,
                    padding: '4px ',
                    textAlign: 'center'
                  }}
                >
                  hover
                </div>
                <div
                  style={{
                    background: colorVar('treeActive'),
                    padding: '4px ',
                    textAlign: 'center'
                  }}
                >
                  active
                </div>
              </div>
              <div style={{ width: 'calc(100% - 240px)' }}>
                <div
                  style={{
                    height: '30px',
                    width: '100%',
                    display: 'flex',
                    fontSize: '11px',
                    lineHeight: '25px',
                    background: colorVar('treeBg'),
                    color: colorVar('text')
                  }}
                >
                  <div
                    style={{
                      background: colorVar('tabBg'),
                      padding: '2px 8px',
                      display: 'flex',
                      alignItems: 'center'
                    }}
                  >
                    normal tab
                    <div
                      style={{
                        marginLeft: '10px',
                        height: '8px',
                        width: '8px',
                        borderRadius: '50%',
                        background: colorVar('tabText')
                      }}
                    ></div>
                  </div>
                  <div
                    style={{
                      background: colorVar('mainBg'),
                      padding: '2px 8px',
                      display: 'flex',
                      alignItems: 'center'
                    }}
                  >
                    active tab
                    <div
                      style={{
                        marginLeft: '10px',
                        cursor: 'pointer'
                      }}
                    >
                      X
                    </div>
                  </div>
                </div>
                <div
                  style={{
                    height: 'calc(100% - 30px)',
                    background: colorVar('mainBg'),
                    color: colorVar('text'),
                    padding: '10px',
                    fontSize: '13px'
                  }}
                >
                  这里得是编辑器, monaco editor
                </div>
              </div>
            </div>
          </div>
        </div>
        <div style={{ padding: '10px', background: '#f2f2f2' }}>
          <div style={{ margin: '10px 0' }}>
            {['Visual Studio Code', '默认浅/深色主题', '颜色对照表'].map((i) => {
              return (
                <div key={i} style={{ textAlign: 'center' }}>
                  {i}
                </div>
              )
            })}
          </div>

          <div style={{ width: '200px' }}>
            {colorVars.map((i) => {
              const color = (i as any)[theme]
              return (
                <div
                  key={i.name}
                  style={{
                    background: color,
                    height: '30px',
                    color: theme === 'dark' || ['text', 'nav'].includes(i.name) ? '#fff' : '#333',
                    padding: '4px',
                    fontSize: '12px',
                    fontWeight: 500,
                    textAlign: 'center'
                  }}
                >
                  {i.desc} {color}
                </div>
              )
            })}
          </div>
          <Button className="mt-10" onClick={toggleTheme}>
            {theme === 'dark' ? '浅色' : '深色'}
          </Button>
        </div>
      </div>
    </>
  )
})
